export default "/**\n *\n * 大家好，我们是湖北大学713实验室\n * 这里，将生成一个我们的介绍网站\n *\n * 请耐心等待介绍网站的生成\n */\n\n\n\n* {\n  -webkit-transition: all 1s;\n  font-size: 16px;\n}\n\n/**\n * 首先，我们来制作一个控制台，在控制台里键入代码\n */\n\nhtml {\n  background: rgb(63, 82, 99);\n}\n\n/***\n * 黑色字体不够清晰？\n * 别急，让我们稍作修改\n */\n\npre, a {\n  color: white;\n}\n\n/**\n * 这样是不是好多了\n * 接下来\n * 不要眨眼哦\n */\n\npre:not(:empty) {\n  overflow: auto;\n  background: rgb(48, 48, 48);\n  border: 1px solid #ccc;\n  max-height: 60vh;\n  width: 49%;\n  font-size: 17px;\n  font-family: monospace;\n  padding: 1vh 0.5vw;\n  box-shadow: -4px 4px 2px 0 rgba(0,0,0,0.3);\n  white-space: pre-wrap;\n  outline: 0;\n  margin: 1vh 0.5vw;\n}\n\n#work-text {\n  max-height: 90vh;\n}\n\n#style-text {\n  -webkit-transform: translateX(95%);\n  position: absolute;\n}\n\n/**\n * 看起来好多了，但是所有的文本都是白色的！！！\n * 所以，我们给特殊的代码添加高亮样式\n * 以获取更好的可读性\n */\n\n.comment       { color: orange; font-style: italic; }\n.selector      { color: #E69F0F; }\n.selector .key { color: #64D5EA; }\n.key           { color: #64D5EA; }\n.value         { color: #BE84F2; }\n.value.px      { color: #F92772; }\n\n/**\n * 这样更易于阅读了\n * 接下来给它加上一些透视效果吧\n */\n\nbody {\n  -webkit-perspective: 1000px;\n}\n\n#style-text {\n  -webkit-transform: translateX(98.5%) rotateY(-10deg);\n  -webkit-transform-origin: right;\n  max-height: 90vh;\n}\n\n/**\n * 接下来，我们将开始介绍713实验室\n * 大家都迫不及待了吧\n * 准备好了吗？\n *\n * 3...\n * 2...\n * 1...\n *\n * ....骗你的😜\n *\n *\n * 好吧，不开玩笑了，我们开始\n */\n\npre:not(#style-text) {\n  -webkit-transform: rotateY(10deg);\n  -webkit-transform-origin: left;\n}\n"